import { Injectable } from '@angular/core';
import { Animations } from './ui.model';

@Injectable({
    providedIn: 'root'
})
export class UiService {
    private _animations: Animations;
    private _modals: any[] = [];

    constructor() {
        this._animations = {};
    }

    public get animations(): Animations {
        return this._animations;
    }
    public set animations(value: Animations) {
        this._animations = value;
    }

    addModal(modal: any) {
        // add modal to array of active modals
        this._modals.push(modal);
    }

    removeModal(id: string) {
        // remove modal from array of active modals
        this._modals = this._modals.filter((x) => x.id !== id);
    }

    openModal(id: string) {
        // open modal specified by id
        const modal = this._modals.find((x) => x.id === id);
        modal.open();
    }

    closeModal(id: string) {
        // close modal specified by id
        const modal = this._modals.find((x) => x.id === id);
        modal.close();
    }
}
