import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ZenidFeedback } from 'libs/zenid-web-sdk/src/lib/zenid.enum';
import { MessageService } from '../../common/services/message.service';
import { ZenidDocumentRoles } from 'libs/zenid-web-sdk/src/lib/zenid.enum';

@Component({
    selector: 'mobile-id-front',
    templateUrl: './front.component.html',
    styleUrls: ['./front.component.scss']
})
export class FrontComponent {
    readonly ZenidFeedback = ZenidFeedback;

    documentRole: keyof typeof ZenidDocumentRoles | 'Sec';
    feedback: keyof typeof ZenidFeedback;
    showSnapNow: boolean;
    multipleCameras: boolean;

    constructor(private route: ActivatedRoute, private messageService: MessageService) {
        this.documentRole = this.route.snapshot.queryParams['documentRole'];
        this.feedback = this.route.snapshot.queryParams['feedback'];
        this.showSnapNow = false;
        this.multipleCameras = false;
    }

    backClicked(): void {
        this.messageService.sendBackPressed();
    }

    cycleCamera(): void {
        // TODO
    }

    snapNow(): void {
        // TODO
    }
}
