import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { SettingsComponent } from './settings.component';
import { ReactiveFormsModule } from '@angular/forms';
import { UiModule } from '@zenid/ui';
import { UtilModule } from '@zenid/util';
import { CountryComponent } from './country/country.component';
import { SelfieVerificationComponent } from './selfie-verification/selfie-verification.component';
import { DocumentsVerifierComponent } from './documents-verifier/documents-verifier.component';
import { DocumentsFilterComponent } from './documents-filter/documents-filter.component';
import { HeaderComponent } from './common/header/header.component';
import { NewFilterComponent } from './documents-filter/new-filter/new-filter.component';
import { NewFilterRoleComponent } from './documents-filter/new-filter/new-filter-role/new-filter-role.component';
import { NewFilterCountryComponent } from './documents-filter/new-filter/new-filter-country/new-filter-country.component';
import { NewFilterPageComponent } from './documents-filter/new-filter/new-filter-page/new-filter-page.component';

const routes: Routes = [
    {
        path: 'settings',
        children: [
            {
                path: '',
                pathMatch: 'full',
                component: SettingsComponent
            },
            {
                path: 'selfie',
                component: SelfieVerificationComponent
            },
            {
                path: 'country',
                component: CountryComponent
            },
            {
                path: 'documents-verifier',
                component: DocumentsVerifierComponent
            },
            {
                path: 'documents-filter',
                children: [
                    {
                        path: '',
                        pathMatch: 'full',
                        component: DocumentsFilterComponent
                    },
                    {
                        path: 'new',
                        children: [
                            {
                                path: '',
                                pathMatch: 'full',
                                component: NewFilterComponent
                            },
                            {
                                path: 'role',
                                component: NewFilterRoleComponent
                            },
                            {
                                path: 'country',
                                component: NewFilterCountryComponent
                            },
                            {
                                path: 'page',
                                component: NewFilterPageComponent
                            }
                        ]
                    }
                ]
            }
        ]
    }
];

@NgModule({
    declarations: [
        SettingsComponent,
        SelfieVerificationComponent,
        CountryComponent,
        DocumentsVerifierComponent,
        DocumentsFilterComponent,
        HeaderComponent,
        NewFilterComponent,
        NewFilterRoleComponent,
        NewFilterCountryComponent,
        NewFilterPageComponent
    ],
    imports: [CommonModule, RouterModule.forChild(routes), ReactiveFormsModule, UiModule, UtilModule]
})
export class SettingsModule {}
