import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SettingsService } from 'apps/mobile/src/app/common/services/settings.service';
import { ZenidContries, zenidContriesFull } from 'libs/zenid-web-sdk/src/lib/zenid.enum';
import { ZenidContryFull } from 'libs/zenid-web-sdk/src/lib/zenid.model';

@Component({
    selector: 'mobile-new-filter-country',
    templateUrl: './new-filter-country.component.html',
    styleUrls: ['./new-filter-country.component.scss']
})
export class NewFilterCountryComponent {
    constructor(private router: Router, private route: ActivatedRoute, private settingsService: SettingsService) {}

    back(): void {
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    selectCountry(country: ZenidContryFull): void {
        this.settingsService.filterForm.get('Country')?.setValue(country.codeString);
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    get countries(): ZenidContryFull[] {
        return zenidContriesFull.sort((a, b) => ('' + a.displayName).localeCompare(b.displayName));
    }
}
