import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LivenessCheckComponent } from './liveness-check.component';
import { InfoComponent } from './info/info.component';
import { CameraComponent } from './camera/camera.component';
import { RouterModule, Routes } from '@angular/router';
import { AngularSvgIconModule } from 'angular-svg-icon';
import { UiModule } from '@zenid/ui';
import { UtilModule } from '@zenid/util';

const routes: Routes = [
    {
        path: 'liveness-check',
        component: LivenessCheckComponent,
        children: [
            {
                path: '',
                redirectTo: 'info',
                pathMatch: 'full'
            },
            {
                path: 'info',
                component: InfoComponent
            },
            {
                path: 'camera',
                component: CameraComponent
            }
        ]
    }
];

@NgModule({
    declarations: [LivenessCheckComponent, InfoComponent, CameraComponent],
    imports: [CommonModule, RouterModule.forChild(routes), AngularSvgIconModule.forRoot(), UiModule, UtilModule]
})
export class LivenessCheckModule {}
