import { Component } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { SettingsService } from 'apps/mobile/src/app/common/services/settings.service';
import { ZenidDocumentRoles } from 'libs/zenid-web-sdk/src/lib/zenid.enum';

@Component({
    selector: 'mobile-new-filter-role',
    templateUrl: './new-filter-role.component.html',
    styleUrls: ['./new-filter-role.component.scss']
})
export class NewFilterRoleComponent {
    constructor(private router: Router, private route: ActivatedRoute, private settingsService: SettingsService) {}

    back(): void {
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    selectRole(role: string): void {
        this.settingsService.filterForm.get('Role')?.setValue(role);
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    get documentRoles(): string[] {
        const rolesFull = Object.values(ZenidDocumentRoles);
        const length = Math.ceil(rolesFull.length / 2);
        return rolesFull.splice(0, length) as string[];
    }
}
