import { Component } from '@angular/core';
import { UntypedFormArray, UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SettingsService } from '../../common/services/settings.service';

@Component({
    selector: 'mobile-documents-filter',
    templateUrl: './documents-filter.component.html',
    styleUrls: ['./documents-filter.component.scss']
})
export class DocumentsFilterComponent {
    constructor(private router: Router, private route: ActivatedRoute, private settingsService: SettingsService) {}

    back(): void {
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    add(): void {
        this.router.navigate(['new'], { relativeTo: this.route });
    }

    get form(): UntypedFormGroup {
        return this.settingsService.form;
    }

    get documentsFilterArray(): UntypedFormArray {
        return this.settingsService.form.get('DocumentsFilter') as UntypedFormArray;
    }

    removeFilter(index: number): void {
        this.settingsService.removeFilter(index);
    }
}
