import { Component, ElementRef, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { UiService } from '@zenid/data-access';

@Component({
    template: ''
})
export class ModalComponent implements OnInit, OnDestroy {
    @Input() id!: string;

    @ViewChild('modal') modal!: ElementRef;

    protected element: HTMLElement;

    opened!: boolean;

    constructor(private el: ElementRef, protected uiService: UiService) {
        this.element = this.el.nativeElement;
    }

    ngOnInit(): void {
        // move element to bottom of page (just before </body>) so it can be displayed above everything else
        document.body.appendChild(this.element);

        // add self (this modal instance) to the modal service so it's accessible from controllers
        this.uiService.addModal(this);
    }

    protected open(): void {
        document.body.style.top = `-${window.scrollY}px`;
        document.body.classList.add('fixed');

        this.modal.nativeElement.classList.remove('hidden');

        // timeout set to 100 in order to css transition work when calling openModal() in another setTimeout
        setTimeout(() => (this.opened = true), 100);
    }

    protected close(): void {
        const scrollY = document.body.style.top;
        document.body.classList.remove('fixed');
        document.body.style.top = '';
        window.scrollTo(0, parseInt(scrollY || '0') * -1);

        this.opened = false;
        setTimeout(() => {
            this.modal.nativeElement.classList.add('hidden');
        }, 300);
    }

    ngOnDestroy(): void {
        this.uiService.removeModal(this.id);
        this.element.remove();
    }
}
