import { Injectable } from '@angular/core';
import { FormArray, FormControl, FormGroup, UntypedFormBuilder } from '@angular/forms';
import { Settings } from '@zenid/util';
import { ZenidWebSdkPossibleDocument } from 'libs/zenid-web-sdk/src/lib/zenid.model';

@Injectable({
    providedIn: 'root'
})
export class SettingsService {
    settings!: Settings;
    form = this.fb.group({
        country: '',
        selfieVerification: '',
        DocumentsFilter: this.fb.array([]),
        DocumentsVerifier: this.fb.group({
            specularAcceptableScore: '',
            documentBlurAcceptableScore: '',
            timeToBlurMaxTolerance: '',
            showTimer: ''
        }),
        Colors: this.fb.group({
            primaryColor: '',
            contrastColor: ''
        }),
        debugMode: ''
    });

    filterForm = this.fb.group({
        Country: null,
        Role: null,
        Page: null
    });

    constructor(private fb: UntypedFormBuilder) {}

    assembleForm(settings: Settings): void {
        // clear form
        this.documentsFilterArray.clear();
        this.form.reset();

        // assemble form
        settings.DocumentsFilter.forEach((value: ZenidWebSdkPossibleDocument) => {
            this.documentsFilterArray.push(this.filterGroup);
        });
        this.form.patchValue(settings);
    }

    removeFilter(index: number): void {
        if (this.documentsFilterArray.length > 1) {
            this.documentsFilterArray.removeAt(index);
        } else {
            this.documentsFilterArray.clear();
        }
    }

    appendFilter(): void {
        const filterGroup = this.filterGroup;
        filterGroup.setValue(this.filterForm.value);
        this.documentsFilterArray.push(filterGroup);
    }

    private get filterGroup(): FormGroup {
        return new FormGroup({
            Role: new FormControl(''),
            Country: new FormControl(''),
            Page: new FormControl('')
        });
    }

    private get documentsFilterArray(): FormArray {
        return this.form.get('DocumentsFilter') as FormArray;
    }
}
