import {
    AfterViewInit,
    Component,
    ElementRef,
    forwardRef,
    HostListener,
    Injector,
    Input,
    OnDestroy,
    ViewChild
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { isDarkMode, TRIGGER_DARK_MODE_EVENT_NAME } from '@zenid/util';
import { Subject, takeUntil } from 'rxjs';
import { InputComponent } from '../input.component';

@Component({
    selector: 'zenid-input-range',
    templateUrl: './input-range.component.html',
    styleUrls: ['./input-range.component.scss'],
    providers: [
        {
            provide: NG_VALUE_ACCESSOR,
            useExisting: forwardRef(() => InputRangeComponent),
            multi: true
        }
    ]
})
export class InputRangeComponent extends InputComponent implements AfterViewInit, OnDestroy {
    @Input() min: number;
    @Input() max: number;

    @ViewChild('input') el!: ElementRef;

    private destroy$: Subject<boolean> = new Subject<boolean>();
    private primaryColor!: string;
    private secondaryColor!: string;

    constructor(injector: Injector) {
        super(injector);
        this.min = 0;
        this.max = 100;
    }

    ngAfterViewInit(): void {
        this.control.valueChanges?.pipe(takeUntil(this.destroy$)).subscribe((value) => {
            this.updateColors(value);
        });
        this.switchDarkModeColors();
    }

    @HostListener('window:' + TRIGGER_DARK_MODE_EVENT_NAME)
    private switchDarkModeColors(): void {
        if (isDarkMode()) {
            this.primaryColor = 'var(--dark-primary-color)';
            this.secondaryColor = 'var(--dark-secondary-color)';
        } else {
            this.primaryColor = 'var(--primary-color)';
            this.secondaryColor = 'var(--secondary-color)';
        }
        this.updateColors(this.control.value);
    }

    private updateColors(value: number): void {
        const val = ((value - this.min) / (this.max - this.min)) * 100;
        const backgroundStyle = `background: linear-gradient(to right, ${this.primaryColor} 0%, ${this.primaryColor} ${val}%, 
            ${this.secondaryColor} ${val}%, ${this.secondaryColor} 100%)`;
        this.el.nativeElement.setAttribute('style', backgroundStyle);
    }

    ngOnDestroy(): void {
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}
