import { Colors } from '..';

export const TRIGGER_DARK_MODE_EVENT_NAME = 'triggerDarkMode';

export const DOCUMENT_ICONS = ['idc', 'drv', 'pas'];
export const DOCUMENT_ICON_UNI = 'uni';
export const POST_MESSAGE_APP_READY = 'post-message-iframe-app-ready';

/*
 * Animations not working on iOS 12 / 13: animate is undefined
 * https://github.com/angular/angular/issues/45016#issuecomment-1050304540
 */
export const disableAnimations =
    !('animate' in document.documentElement) || (navigator && /iPhone OS (8|9|10|11|12|13)_/.test(navigator.userAgent));

export const lowercaseFirstLetter = (string: string): string => {
    return string.charAt(0).toLowerCase() + string.slice(1);
};

const camelCaseToCssDash = (string: string): string => {
    return '--' + string.replace(/[A-Z]/g, (m) => '-' + m.toLowerCase());
};

export const setCssVar = (variable: string, value: string): void => {
    document.documentElement.style.setProperty(variable, value);
};

export const setThemeColors = (colors: Colors): void => {
    Object.keys(colors).forEach((key) => {
        const colorValue = colors[key as keyof Colors].toString();
        if (colorValue) {
            const colorName = camelCaseToCssDash(key);
            setCssVar(colorName, colorValue);
        }
    });
};

export const triggerDarkMode = (darkMode: boolean): void => {
    if (darkMode) {
        document.documentElement.classList.add('dark');
    } else {
        document.documentElement.classList.remove('dark');
    }
    // dispatch CustomEvent if needed (for example in input-range.component)
    window.dispatchEvent(new CustomEvent(TRIGGER_DARK_MODE_EVENT_NAME));
};

export const isDarkMode = (): boolean => document.documentElement.classList.contains('dark');

export const lightenColor = (color: string, percent: number): string => {
    const num = parseInt(color.replace('#', ''), 16),
        amt = Math.round(2.55 * percent),
        R = (num >> 16) + amt,
        B = ((num >> 8) & 0x00ff) + amt,
        G = (num & 0x0000ff) + amt;

    return (
        '#' +
        (
            0x1000000 +
            (R < 255 ? (R < 1 ? 0 : R) : 255) * 0x10000 +
            (B < 255 ? (B < 1 ? 0 : B) : 255) * 0x100 +
            (G < 255 ? (G < 1 ? 0 : G) : 255)
        )
            .toString(16)
            .slice(1)
    );
};
