import { Component, Inject } from '@angular/core';
import { CommonSdkService } from '@zenid/common';
import { UiService } from '@zenid/data-access';

@Component({
    selector: 'zenid-access',
    templateUrl: './camera-access.component.html',
    styleUrls: ['./camera-access.component.scss']
})
export class CameraAccessComponent {
    animationSrc: string;
    cameraTested = true;

    constructor(private uiService: UiService, @Inject('SdkService') private sdkService: CommonSdkService) {
        this.animationSrc = JSON.stringify(this.uiService.animations['camera']);
    }

    onOk(): void {
        this.cameraTested = false;
        this.sdkService.testCamera().subscribe({
            next: () => {
                this.cameraTested = true;
                this.sdkService.testCameraOk();
            },
            error: () => {
                this.cameraTested = true;
                this.sdkService.testCameraOk();
                // TODO: handle testCameraError
            }
        });
    }
}
