import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SelfieComponent } from './selfie.component';
import { InfoComponent } from './info/info.component';
import { CameraComponent } from './camera/camera.component';
import { CheckComponent } from './check/check.component';
import { RouterModule, Routes } from '@angular/router';
import { AngularSvgIconModule } from 'angular-svg-icon';
import { UiModule } from '@zenid/ui';
import { UtilModule } from '@zenid/util';

const routes: Routes = [
    {
        path: 'selfie',
        component: SelfieComponent,
        children: [
            {
                path: '',
                redirectTo: 'info',
                pathMatch: 'full'
            },
            {
                path: 'info',
                component: InfoComponent
            },
            {
                path: 'camera',
                component: CameraComponent
            },
            {
                path: 'check',
                component: CheckComponent
            }
        ]
    }
];

@NgModule({
    declarations: [SelfieComponent, InfoComponent, CameraComponent, CheckComponent],
    imports: [CommonModule, RouterModule.forChild(routes), AngularSvgIconModule.forRoot(), UiModule, UtilModule],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class SelfieModule {}
