import { Component } from '@angular/core';
import { UiService } from '@zenid/data-access';
import { Settings } from '@zenid/util';
import { MessageService } from '../common/services/message.service';

@Component({
    selector: 'mobile-analyzing',
    templateUrl: './analyzing.component.html',
    styleUrls: ['./analyzing.component.scss']
})
export class AnalyzingComponent {
    animationSrc: string;
    selfieVerification: Settings['selfieVerification'];
    statusesSelfieForShow: Settings['selfieVerification'][];
    showExpanded: boolean;

    constructor(private uiService: UiService, private messageService: MessageService) {
        this.animationSrc = JSON.stringify(this.uiService.animations['analyzing']);
        this.statusesSelfieForShow = ['selfie', 'liveness', 'livenessLegacy'];
        if (this.messageService.dataToSend.previousEvent?.data?.selfieVerification) {
            this.selfieVerification = this.messageService.dataToSend.previousEvent?.data?.selfieVerification;
            this.showExpanded = this.statusesSelfieForShow.includes(this.selfieVerification);
        } else {
            this.selfieVerification = 'none';
            this.showExpanded = false;
        }
    }
}
