import { AfterViewInit, Component, ElementRef, EventEmitter, Input, Output, ViewChild } from '@angular/core';
import { UiService } from '@zenid/data-access';

export enum AlertTypesEnum {
    Success,
    Error,
    Warning
}

@Component({
    selector: 'zenid-alert',
    templateUrl: './alert.component.html',
    styleUrls: ['./alert.component.scss']
})
export class AlertComponent implements AfterViewInit {
    @Input() type!: number;
    @Input() title!: string;
    @Input() desc!: string;
    @Input() errorText?: string;
    @Input() buttonTitle?: string;
    @Input() buttonWithIcon = false;

    @ViewChild('player') player!: ElementRef;

    private readonly assetMapping = ['success', 'error', 'warning'];

    @Output() next: EventEmitter<undefined> = new EventEmitter();

    constructor(private uiService: UiService) {}

    ngAfterViewInit(): void {
        const player = this.player.nativeElement;
        // Temp fix - sometimes load ends with error
        setTimeout(() => player.load(this.uiService.animations[this.assetMapping[this.type]]));
    }

    nextClicked(): void {
        this.next.emit();
    }
}
