import { APP_INITIALIZER, CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';

import { AppComponent } from './app.component';
import { RouterModule, Routes } from '@angular/router';
import { DocumentModule } from './document/document.module';
import { UtilModule } from '@zenid/util';
import { ReactiveFormsModule } from '@angular/forms';
import { UiModule } from '@zenid/ui';
import { TRANSLOCO_SCOPE } from '@ngneat/transloco';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { WelcomeComponent } from './welcome/welcome.component';
import { ScanComponent } from './scan/scan.component';
import { HomeComponent } from './home/home.component';
import { RootInterceptor } from './common/interceptors/root.interceptor';
import { AngularSvgIconModule } from 'angular-svg-icon';
import { from, Observable, tap } from 'rxjs';
import { environment } from '../environments/environment';
import { CameraComponent } from './camera/camera.component';
import { SettingsModule } from './settings/settings.module';
import { SelfieModule } from './selfie/selfie.module';
import { LivenessCheckModule } from './liveness-check/liveness-check.module';
import { LoadingComponent } from './loading/loading.component';
import { AnalyzingComponent } from './analyzing/analyzing.component';
import { ResultsComponent } from './results/results.component';

const routes: Routes = [
    { path: '', component: AppComponent },
    { path: 'welcome', component: WelcomeComponent },
    { path: 'camera', component: CameraComponent },
    { path: 'scan', component: ScanComponent },
    { path: 'home', component: HomeComponent },
    { path: 'loading', component: LoadingComponent },
    { path: 'analyzing', component: AnalyzingComponent },
    { path: 'results', component: ResultsComponent }
];

export interface AppConfig {
    appRoot?: string;
}

const loadFonts = (): (() => Observable<any>) => {
    return () => {
        const functionFont = new FontFace(
            'coolicons',
            `url(\'${environment.config.appRoot}/assets/fonts/coolicons/coolicons.woff?fs2eqh\') format(\'woff\')`
        );
        return from(functionFont.load()).pipe(tap((loadedFace) => (document as any).fonts.add(loadedFace)));
    };
};

@NgModule({
    declarations: [
        AppComponent,
        WelcomeComponent,
        ScanComponent,
        HomeComponent,
        CameraComponent,
        LoadingComponent,
        AnalyzingComponent,
        ResultsComponent
    ],
    imports: [
        BrowserModule,
        RouterModule.forRoot(routes, { initialNavigation: 'disabled' }),
        DocumentModule,
        SelfieModule,
        LivenessCheckModule,
        UtilModule,
        ReactiveFormsModule,
        UiModule,
        AngularSvgIconModule.forRoot(),
        SettingsModule
    ],
    providers: [
        { provide: APP_INITIALIZER, useFactory: loadFonts, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: RootInterceptor, multi: true },
        { provide: TRANSLOCO_SCOPE, useValue: 'web-demo' }
    ],
    bootstrap: [AppComponent],
    exports: [RouterModule],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class AppModule {}
