import { Component, Inject } from '@angular/core';
import { CommonSdkService } from '@zenid/common';
import { UiService } from '@zenid/data-access';
import { Settings } from '@zenid/util';

@Component({
    selector: 'zenid-analyzing',
    templateUrl: './analyzing.component.html',
    styleUrls: ['./analyzing.component.scss']
})
export class AnalyzingComponent {
    animationSrc: string;
    selfieVerification: Settings['selfieVerification'];
    statusesSelfieForShow: Settings['selfieVerification'][] = ['selfie', 'liveness', 'livenessLegacy'];
    showExpanded: boolean;

    constructor(private uiService: UiService, @Inject('SdkService') private sdkService: CommonSdkService) {
        this.animationSrc = JSON.stringify(this.uiService.animations['analyzing']);
        this.selfieVerification = this.sdkService.settings.selfieVerification;
        this.showExpanded = this.statusesSelfieForShow.includes(this.selfieVerification);
    }
}
