import { HttpClient, HttpClientModule } from '@angular/common/http';
import {
    TRANSLOCO_LOADER,
    Translation,
    TranslocoLoader,
    TRANSLOCO_CONFIG,
    translocoConfig,
    TranslocoModule,
    TranslocoService
} from '@ngneat/transloco';
import { APP_INITIALIZER, Injectable, NgModule } from '@angular/core';
import { Observable } from 'rxjs';

export const availableLangs = ['en', 'cs', 'sk', 'hu'];
const getDefaultLang = () => {
    let defaultLang;
    navigator.languages.every((language) => {
        defaultLang = availableLangs.find((lang) => {
            return lang === language.toLowerCase().substring(0, 2);
        });
        return defaultLang ? false : true;
    });
    return defaultLang;
};

@Injectable({ providedIn: 'root' })
export class TranslocoHttpLoader implements TranslocoLoader {
    constructor(private http: HttpClient) {}

    getTranslation(lang: string) {
        return this.http.get<Translation>(`/assets/i18n/${lang}.json`);
    }
}

const preloadLang = (transloco: TranslocoService): (() => Observable<Translation>) => {
    return () => transloco.load(transloco.getDefaultLang());
};

@NgModule({
    exports: [TranslocoModule],
    imports: [HttpClientModule],
    providers: [
        {
            provide: TRANSLOCO_CONFIG,
            useValue: translocoConfig({
                availableLangs: availableLangs,
                defaultLang: getDefaultLang() ? getDefaultLang() : 'en',
                reRenderOnLangChange: true,
                prodMode: true
            })
        },
        { provide: TRANSLOCO_LOADER, useClass: TranslocoHttpLoader },
        {
            provide: APP_INITIALIZER,
            multi: true,
            useFactory: preloadLang,
            deps: [TranslocoService]
        }
    ]
})
export class TranslocoRootModule {}
