import { CUSTOM_ELEMENTS_SCHEMA, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { DocumentComponent } from './document.component';
import { FrontComponent } from './front/front.component';
import { FrontCheckComponent } from './front-check/front-check.component';
import { BackComponent } from './back/back.component';
import { BackCheckComponent } from './back-check/back-check.component';
import { UiModule } from '@zenid/ui';
import { UtilModule } from '@zenid/util';
import { InfoComponent } from './info/info.component';
import { AngularSvgIconModule } from 'angular-svg-icon';

const routes: Routes = [
    {
        path: 'document',
        component: DocumentComponent,
        children: [
            {
                path: '',
                redirectTo: 'info',
                pathMatch: 'full'
            },
            {
                path: 'info',
                component: InfoComponent
            },
            {
                path: 'front',
                component: FrontComponent
            },
            {
                path: 'front-check',
                component: FrontCheckComponent
            },
            {
                path: 'back',
                component: BackComponent
            },
            {
                path: 'back-check',
                component: BackCheckComponent
            }
        ]
    }
];

@NgModule({
    declarations: [
        DocumentComponent,
        FrontComponent,
        FrontCheckComponent,
        BackComponent,
        BackCheckComponent,
        InfoComponent
    ],
    imports: [CommonModule, RouterModule.forChild(routes), AngularSvgIconModule.forRoot(), UiModule, UtilModule],
    schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class DocumentModule {}
