import { Component } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { zenidContriesFull } from 'libs/zenid-web-sdk/src/lib/zenid.enum';
import { SettingsService } from '../../../common/services/settings.service';

@Component({
    selector: 'mobile-new-filter',
    templateUrl: './new-filter.component.html',
    styleUrls: ['./new-filter.component.scss']
})
export class NewFilterComponent {
    constructor(private router: Router, private route: ActivatedRoute, private settingsService: SettingsService) {}

    back(): void {
        this.settingsService.filterForm.reset();
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    submit(): void {
        this.settingsService.appendFilter();
        this.settingsService.filterForm.reset();
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    getCountryDisplayName(codeString: string): string {
        const contryDisplayName = zenidContriesFull.find((country) => country.codeString === codeString)?.displayName;
        return contryDisplayName ? contryDisplayName : '';
    }

    get filterForm(): FormGroup {
        return this.settingsService.filterForm;
    }
}
