import { Component } from '@angular/core';
import { FormArray, UntypedFormGroup } from '@angular/forms';
import { zenidContriesFull } from 'libs/zenid-web-sdk/src/lib/zenid.enum';
import { MessageService } from '../common/services/message.service';
import { SettingsService } from '../common/services/settings.service';

@Component({
    selector: 'mobile-settings',
    templateUrl: './settings.component.html',
    styleUrls: ['./settings.component.scss']
})
export class SettingsComponent {
    constructor(private messageService: MessageService, private settingsService: SettingsService) {}

    back(): void {
        this.messageService.sendBackPressed({ data: this.settingsService.form.value });
    }

    get documentsFilterArray(): FormArray {
        return this.settingsService.form.get('DocumentsFilter') as FormArray;
    }

    get form(): UntypedFormGroup {
        return this.settingsService.form;
    }

    getCountryDisplayName(codeString: string): string {
        const contryDisplayName = zenidContriesFull.find((country) => country.codeString === codeString)?.displayName;
        return contryDisplayName ? contryDisplayName : '';
    }
}
