import {
    ZenidWebSdkDocumentSettings,
    ZenidWebSdkPossibleDocument,
    ZenidWebSdkSelfieVideoSettings
} from '@zenid/zenid-web-sdk';

interface DocumentsVerifier {
    specularAcceptableScore: number;
    documentBlurAcceptableScore: number;
    timeToBlurMaxTolerance: number;
    readBarcode: boolean;
    showTimer: boolean;
    enableAimingCircle: boolean;
    drawOutline: boolean;
    displaySnapNowDelay: number;
    displayHelpModalDelay: number;
}

export interface Colors {
    primaryColor: string;
    primaryColorDarken: string;
    secondaryColor: string;
    contrastColor: string;
    darkPrimaryColor: string;
    darkPrimaryColorDarken: string;
    darkSecondaryColor: string;
    darkContrastColor: string;
}

export interface Settings {
    country: number | string;
    selfieVerification: 'none' | 'selfie' | 'liveness' | 'livenessLegacy';
    livenessCheckVideo: boolean;
    secondDocuments: Array<number>;
    DocumentsFilter: Array<ZenidWebSdkPossibleDocument>;
    DocumentsVerifier: DocumentsVerifier;
    Colors: Colors;
    darkMode: boolean;
    debugMode: boolean;
}

export interface PartialSettings {
    country: number | string;
    selfieVerification: 'none' | 'selfie' | 'liveness' | 'livenessLegacy';
    livenessCheckVideo: boolean;
    DocumentsVerifier: DocumentsVerifier;
    Colors: Colors;
    darkMode: boolean;
    debugMode: boolean;
}

export type SettingsReferer = 'welcome' | 'home' | null;

export interface LocalStorageSettings {
    settingsReferer: SettingsReferer;
    settings: Settings;
}

export const settingsToDocumentSettings = (settings: Settings): ZenidWebSdkDocumentSettings => {
    return {
        feature: FeaturesEnum.DOCUMENT,
        enabledModels: {
            PossibleDocuments: [...settings.DocumentsFilter, { Country: settings.country }]
        },
        visualizer: new Visualizer({ drawText: false }),
        specularAcceptableScore: settings.DocumentsVerifier.specularAcceptableScore,
        documentBlurAcceptableScore: settings.DocumentsVerifier.documentBlurAcceptableScore,
        timeToBlurMaxTolerance: settings.DocumentsVerifier.timeToBlurMaxTolerance,
        readBarcode: settings.DocumentsVerifier.readBarcode,
        showTimer: settings.DocumentsVerifier.showTimer,
        enableAimingCircle: settings.DocumentsVerifier.enableAimingCircle,
        drawOutline: settings.DocumentsVerifier.drawOutline
    };
};

export const settingsToSelfieVideoSettings = (settings: Settings): ZenidWebSdkSelfieVideoSettings => {
    return {
        feature: settings.selfieVerification === FeaturesEnum.SELFIE ? FeaturesEnum.SELFIE : FeaturesEnum.SELFIE_VIDEO,
        enableLegacyMode: settings.selfieVerification === 'livenessLegacy',
        visualizer: new Visualizer({ drawText: false })
    };
};

export enum FeaturesEnum {
    DOCUMENT = 'document',
    SELFIE = 'selfie',
    SELFIE_VIDEO = 'selfieVideo'
}
