import { Component } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MessageService } from '../../common/services/message.service';
import { ZenidDocumentRoles } from 'libs/zenid-web-sdk/src/lib/zenid.enum';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
    selector: 'mobile-back-check',
    templateUrl: './back-check.component.html',
    styleUrls: ['./back-check.component.scss']
})
export class BackCheckComponent {
    documentRole: keyof typeof ZenidDocumentRoles | 'Sec';
    imagePath: SafeResourceUrl;

    constructor(private route: ActivatedRoute, public messageService: MessageService, private sanitizer: DomSanitizer) {
        this.documentRole = this.route.snapshot.queryParams['documentRole'];
        this.imagePath = this.sanitizer.bypassSecurityTrustResourceUrl(
            'data:image/jpg;base64,' + this.messageService.dataToSend.previousEvent?.base64
        );
    }

    backClicked(): void {
        this.messageService.sendBackPressed();
    }

    submit(): void {
        this.messageService.sendNextPressed();
    }
}
