import { HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'apps/mobile/src/environments/environment';
import { Observable } from 'rxjs';

@Injectable()
export class RootInterceptor implements HttpInterceptor {
    private readonly baseAssetsPath = '/assets';

    intercept(req: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        if (req.url.startsWith(this.baseAssetsPath)) {
            const newReq = req.clone({
                url: environment.config.appRoot + req.url
            });
            return next.handle(newReq);
        }
        return next.handle(req);
    }
}
