import { APP_INITIALIZER, NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { TranslocoRootModule } from './transloco/transloco-root.module';
import {
    disableAnimations,
    setCssVar,
    setThemeColors,
    triggerDarkMode,
    lightenColor,
    lowercaseFirstLetter,
    isDarkMode,
    TRIGGER_DARK_MODE_EVENT_NAME,
    DOCUMENT_ICONS,
    DOCUMENT_ICON_UNI,
    POST_MESSAGE_APP_READY
} from './common';
import { HttpClient } from '@angular/common/http';
import { forkJoin, Observable, tap } from 'rxjs';
import { Animations, DataAccessModule, UiService } from '@zenid/data-access';
import { fadeAnimation, getRouterOutletState } from './animations';

const loadAnimations = (httpClient: HttpClient, utilService: UiService): (() => Observable<any>) => {
    const animationsToLoad = ['analyzing', 'camera', 'error', 'id-back', 'loading', 'success', 'warning', 'welcome'];
    const obs: { [key: string]: Observable<any> } = {};
    animationsToLoad.forEach((value: string) => {
        obs[value] = httpClient.get(`/assets/animations/${value}.json`);
    });

    return () => forkJoin(obs).pipe(tap((animations: Animations) => (utilService.animations = animations)));
};

@NgModule({
    imports: [CommonModule, DataAccessModule],
    exports: [TranslocoRootModule],
    providers: [{ provide: APP_INITIALIZER, useFactory: loadAnimations, deps: [HttpClient, UiService], multi: true }]
})
export class UtilModule {}

export {
    // animations
    fadeAnimation,
    getRouterOutletState,
    // common
    TRIGGER_DARK_MODE_EVENT_NAME,
    POST_MESSAGE_APP_READY,
    DOCUMENT_ICONS,
    DOCUMENT_ICON_UNI,
    disableAnimations,
    lowercaseFirstLetter,
    setCssVar,
    triggerDarkMode,
    isDarkMode,
    setThemeColors,
    lightenColor
};
