import { Component } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { SettingsService } from '../../common/services/settings.service';

@Component({
    selector: 'mobile-documents-verifier',
    templateUrl: './documents-verifier.component.html',
    styleUrls: ['./documents-verifier.component.scss']
})
export class DocumentsVerifierComponent {
    constructor(private router: Router, private route: ActivatedRoute, private settingsService: SettingsService) {}

    back(): void {
        this.router.navigate(['..'], { relativeTo: this.route });
    }

    get form(): UntypedFormGroup {
        return this.settingsService.form;
    }
}
