import { Component } from '@angular/core';
import { UiService } from '@zenid/data-access';
import { MessageService } from '../common/services/message.service';

@Component({
    selector: 'mobile-camera',
    templateUrl: './camera.component.html',
    styleUrls: ['./camera.component.scss']
})
export class CameraComponent {
    animationSrc!: string;

    constructor(private uiService: UiService, private messageService: MessageService) {
        this.animationSrc = JSON.stringify(this.uiService.animations['camera']);
    }

    onOk(): void {
        this.messageService.sendNextPressed();
    }
}
